@use 'base/mixins' as m;
@use 'base/variables' as v;

.lineup{
    background-color: v.$rosa;
    padding: 5rem 0;
    
    h3, p{
        text-align: center;
    }

    .dia{
        text-transform: uppercase;
        font-weight: 600;
        margin: 5rem 0;
    }

}

.bg-beige{
    background-color: v.$beige;

    li:nth-child(odd){
        background-color: darken(v.$beige, 5%);
    }
}

.bg-verde{
    background-color: v.$verde;
    li:nth-child(odd){
        background-color: darken(v.$verde, 5%);
    }
}

.escenarios-contenedor{
    @include m.grid(1, 0rem);
    

    // @include m.desktop {
    //     @include m.grid (2, 5rem);   
    // } 

    @include m.tablet {
        @include m.grid (2, 5rem);  
        
    }
}

.escenario{
    padding-top: 2rem;

    p{
        &:after {
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 2rem auto 0 auto;
        }
    }
}

.edm .nombre-escenario:after {
    background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after {
    background-image: url(../img/icono_rock.png);
}

.calendario{
    @include m.resetear-lista;

    li{
        padding: 2.5rem;
        font-weight: 700;

        span{
            font-weight: 400;
        }
    }
    
}