html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.7;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #FEBE8C;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
  }
}
.header h1 {
  color: #fff;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  margin: 2rem;
  color: #fff;
  font-size: 2.5rem;
  margin-bottom: 2rem;
  font-weight: bold;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #F7A4A4;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(2, 0, 36);
  background: linear-gradient(259deg, rgb(2, 0, 36) 0%, rgba(165, 3, 255, 0.5) 35%, rgb(0, 212, 255) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 1200px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 10;
}
.contenido-video h2 {
  color: #fff;
  font-size: 6rem;
  margin: 0;
}
.contenido-video p {
  background-color: #F7A4A4;
  display: inline-block;
  color: #fff;
  padding: 1rem 2rem;
  font-weight: 700;
  font-size: 2.5rem;
  margin: 0;
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  margin-bottom: 2rem;
}
.contenido-festival .fecha {
  color: #F7A4A4;
  font-weight: 700;
}

.lineup {
  background-color: #F7A4A4;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
}
.lineup .dia {
  text-transform: uppercase;
  font-weight: 600;
  margin: 5rem 0;
}

.bg-beige {
  background-color: #FFFBC1;
}
.bg-beige li:nth-child(odd) {
  background-color: #fff9a8;
}

.bg-verde {
  background-color: #B6E2A1;
}
.bg-verde li:nth-child(odd) {
  background-color: #a7dc8e;
}

.escenarios-contenedor {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0rem;
}
@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.escenario {
  padding-top: 2rem;
}
.escenario p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after {
  background-image: url(../img/icono_rock.png);
}

.calendario {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.15);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  background-color: #fff;
  width: 5rem;
  height: 5rem;
  border-radius: 60%;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.bg-rosa {
  background-color: #F7A4A4;
}

.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #fff;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase .nombre-pase {
  text-align: center;
  font-size: 2.8rem;
  color: #fff;
}
.pase-header, .pase-footer {
  padding: 0.5rem;
}
.pase-header p, .pase-footer p {
  text-align: center;
  color: #fff;
  font-weight: 700;
  margin: 0;
}
.pase-header {
  background-color: #B6E2A1;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase-body {
  background-color: #FFFBC1;
}
.pase-body ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.pase-body ul li,
.pase-body ul :nth-child(odd) {
  background-color: #fff;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: #FEBE8C;
}
.pase-footer p {
  text-transform: uppercase;
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  background-color: #B6E2A1;
  padding: 1rem;
}
.site-footer p {
  color: #000000;
  margin: 0;
  text-align: center;
  font-weight: 600;
}