@use 'base/mixins' as m;
@use 'base/variables' as v;

.bg-rosa{
    background-color: v.$rosa;
}

.boletos{
    padding: 5rem 0;

    h3{
        text-align: center;  
        color: v.$blanco;
        margin-bottom: 2rem;
    }

    .contenedor-boletos{
        @include m.tablet {
            @include m.grid(2, 5rem)
        }
        
    }
}

.pase{
    .nombre-pase{
        text-align: center;
        font-size: 2.8rem;
        color: v.$blanco;
    }

    &-header,
    &-footer{
        padding: .5rem;

        p{
            text-align: center;
            color: v.$blanco;
            font-weight: 700;
            margin: 0;
        }
    }

    &-header{
        background-color: v.$verde;
        p{
            text-transform: uppercase;
            font-size: 1.6rem;
        }
    }
    &-body{
        background-color: v.$beige;
        ul{
            @include m.resetear-lista;

            li,
            :nth-child(odd){
                background-color: v.$blanco;
                padding: 1rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }
        
    }
    &-footer{
        background-color: v.$naranja;
        p{
            text-transform: uppercase;
            font-size: 2.4rem;
            font-weight: 700;
        }
    }
}
