@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    background-color: v.$naranja;

    @include m.tablet {
        &.fijo{
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
        }

    }
    
    

    h1{
        color: v.$blanco;
        text-align: center;
        @include m.tablet {
            margin: 0;
        }
    }

    .contenido-header{
        padding: 2rem;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        
    }
}

.navegacion-principal{
    @include m.tablet {
        display: flex;
        gap: 2rem;
    }
    
    a{
        display: block;
        text-align: center;
        margin: 2rem;
        color: v.$blanco;
        font-size: 2.5rem;
        margin-bottom: 2rem;
        font-weight: bold;

        @include m.tablet {
            margin: 0;
        }
        

        &:hover{
            color: v.$rosa;
        }
    }
}

