@use 'base/variables' as v;
@use 'base/mixins' as m;

.video{
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;

    .overlay{
        position: absolute;
        background: rgb(2,0,36);
        background: linear-gradient(259deg, rgba(2,0,36,1) 0%, rgba(165,3,255,0.5) 35%, rgba(0,212,255,1) 100%);         
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;

    }

    video{
        width: auto;
        display: block;
        @include m.desktop {
            width: 100%; 
        }
        
    }
}

.contenido-video{
    z-index: 10;
    h2{
        color: v.$blanco;
        font-size: 6rem;
        margin: 0;
    }
    p{
        background-color: v.$rosa;
        display: inline-block;
        color: v.$blanco;
        padding: 1rem 2rem;
        font-weight: 700;
        font-size: 2.5rem;
        margin: 0;

    }
}
